@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;700&display=swap);
.switch-wrapper {
  position: relative;
  width: 100vw;
}

.switch-wrapper > div {
  position: absolute;
  width: 100%;
}

.Home {
  height: 100vh;
  display: grid;
  place-items: center;
  overflow: hidden;
}

.wrapper {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.title {
  font-size: 2rem;
  color: #fff;
  font-weight: 300;
}

.gif {
  width: 80vw;
  max-width: 300px;
  margin: 50px 0;
}

.left-buttons,
.right-buttons {
  position: absolute;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  left: 80px;
  top: 30%;
}

.right-buttons {
  left: auto;
  right: 80px;
  top: 70%;
}
.right-buttons p {
  color: #fff;
  margin-bottom: 10px;
}

.right-buttons .link-btn:hover {
  -webkit-transform: rotate(-1deg);
          transform: rotate(-1deg);
}

.link-btn {
  background-color: #fff;
  color: black;
  display: -webkit-flex;
  display: flex;
  padding: 10px 20px;
  text-decoration: none;
  -webkit-align-items: center;
          align-items: center;
  box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.2);
  margin-bottom: 5px;
  cursor: pointer;
}

.link-btn:hover {
  -webkit-transform: rotate(1deg);
          transform: rotate(1deg);
}

.link-btn .icon {
  margin-right: 10px;
}

.github svg {
  z-index: 99;
}

@media (min-width: 1600px) {
  .title {
    font-size: 2.5rem;
  }
  .gif {
    max-width: 500px;
  }
  .left-buttons {
    left: 200px;
    font-size: 1.5rem;
  }
  .right-buttons {
    font-size: 1.5rem;
    right: 200px;
  }
  .Languages li {
    font-size: 1.8rem;
  }
  .CVButton {
    font-size: 1.5rem !important;
  }
  .github-corner svg {
    width: 120px;
    height: 120px;
  }
  .HomeButton {
    width: 100px !important;
    height: 100px !important;
  }

  .HomeButton svg {
    width: 64px;
  }
}

@media (max-width: 991.98px) {
  .right-buttons {
    display: none;
  }
}

@media (max-width: 767.98px) {
  .left-buttons {
    left: 0;
    -webkit-align-items: baseline;
            align-items: baseline;
  }
  .btn-text {
    display: none !important;
  }
}

@media (max-width: 500px) {
  .Home {
    text-align: center;
  }
  .gif {
    max-width: 300px;
  }
}

.Languages {
  position: absolute;
  padding: 10px;
  color: #ccc;
  z-index: 1;
}

ul {
  display: -webkit-flex;
  display: flex;
  list-style-type: none;
}

li {
  margin: 0 5px;
  cursor: pointer;
  font-size: 1.2rem;
}

li:hover {
  opacity: 0.8;
}

.active:hover {
  opacity: 1;
}

.active {
  color: #fff;
}

.ParticlesBG {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #3664f4;
  z-index: -1;
}

.About {
  height: 100vh;
  position: relative;
  color: #fff;
  display: grid;
  grid-template-columns: 35% 65%;
}

.About .HomeButton {
  left: auto;
  right: 25px;
  bottom: 25px;
}

.about-side {
  background-color: #fff;
  padding: 20px;
  color: #3664f4;
  height: 100%;
  box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.2);
}

.about-side .bio {
  color: #000;
  margin-top: 30px;
}

.about-side .bio p {
  margin-top: 10px;
}

.about-side .title-line {
  background-color: #3664f4;
}

.right-side {
  display: grid;
  place-items: center;
}

.quote {
  font-size: 1.2rem;
}

@media (min-width: 1600px) {
  .about-side h1 {
    font-size: 2.5rem;
  }

  .about-side h3 {
    font-size: 1.6rem;
  }

  .about-side {
    font-size: 1.3rem;
  }
  .quote {
    font-size: 1.8rem;
  }
}

@media (max-width: 991.98px) {
  .About {
    grid-template-columns: 100% 0;
    padding: 10px;
  }
  .right-side {
    display: none;
  }
}

.HomeButton {
  display: grid;
  place-items: center;
  padding: 10px;
  border-radius: 50%;
  position: absolute;
  bottom: 25px;
  left: 25px;
  background-color: white;
  text-decoration: none;
  width: 64px;
  height: 64px;
  box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.1);
  z-index: 100;
}

.HomeButton:hover {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}

.Skills {
  margin-top: 50px;
}

.Skills section {
  margin: 10px 0;
}

.Skills section ul {
  color: #000;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.Skills section ul li {
  margin-right: 10px;
  cursor: auto;
}

.Projects {
  padding: 20px;
  color: #fff;
  height: 100vh;
}

.projects-title {
  font-weight: bold;
}

.title-line {
  width: 4rem;
  height: 3px;
  background-color: #fff;
  margin-top: 10px;
  margin-bottom: 20px;
}

.slider {
  height: calc(100% - 150px);
  padding: 10px;
  display: -webkit-flex !important;
  display: flex !important;
  -webkit-justify-content: center !important;
          justify-content: center !important;
  -webkit-align-items: center !important;
          align-items: center !important;
}

.slider .slick-list,
.slider .slick-track,
.slider .slick-slide,
.slider .slick-slide,
.slider .slick-slide > div {
  height: 100%;
}

.slider .desktop {
  grid-template-columns: 40% 60%;
}

.slick-dots {
  position: fixed !important;
  bottom: 50px !important;
  left: -10px;
  right: auto;
}

.slick-dots li button:before {
  color: white !important;
  font-size: 14px !important;
}

@media (min-width: 1600px) {
  .projects-title {
    font-size: 2.5rem;
  }

  .HomeButton {
    width: 100px !important;
    height: 100px !important;
  }

  .HomeButton svg {
    width: 64px;
  }
  .project-info h1 {
    font-size: 2.5rem;
  }
  .project-info p {
    font-size: 1.2rem;
  }
  .link-btn {
    font-size: 1.5rem;
  }
  .project-picture img {
    width: 60% !important;
    max-height: 400px !important;
  }
}

@media (max-width: 767.98px) {
  .Project {
    grid-template-columns: 100% 0 !important;
  }
  .project-picture {
    display: none !important;
  }
}

.Project {
  display: grid !important;
  grid-column-gap: 1rem;
  -webkit-column-gap: 1rem;
          column-gap: 1rem;
  grid-template-columns: 60% 40%;
  cursor: -webkit-grab;
  cursor: grab;
  height: 100%;
}

.Project:active {
  cursor: -webkit-grabbing;
  cursor: grabbing;
}

.project-info {
  text-align: center;
  display: grid;
  place-items: center;
  padding: 0 10px;
}

.project-buttons {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  margin: 0 auto;
  margin-top: 50px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

.project-picture {
  display: grid;
  place-items: center;
}

.project-picture img {
  width: 60%;
  max-height: 300px;
  object-fit: contain;
}

.project-buttons .link-btn {
  margin: 0 5px;
}

.project-buttons .link-btn svg {
  margin-right: 5px;
}

.project:focus {
  outline: none;
  border: none;
}

.project-line {
  width: 100px;
  margin-left: auto;
  margin-right: auto;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Montserrat", "sans-serif";
}

body {
  overflow: hidden;
}

