.Languages {
  position: absolute;
  padding: 10px;
  color: #ccc;
  z-index: 1;
}

ul {
  display: flex;
  list-style-type: none;
}

li {
  margin: 0 5px;
  cursor: pointer;
  font-size: 1.2rem;
}

li:hover {
  opacity: 0.8;
}

.active:hover {
  opacity: 1;
}

.active {
  color: #fff;
}
